'use client'

import { useState, useEffect } from 'react'
import { analyticsApi, endpointsApi, ApiError } from '@/lib/api-client'
import { BarChart3, Loader2, AlertCircle, TrendingUp, Clock, CheckCircle, DollarSign } from 'lucide-react'

export default function AnalyticsPage() {
  const [endpoints, setEndpoints] = useState<any[]>([])
  const [selectedEndpoint, setSelectedEndpoint] = useState<string | null>(null)
  const [analytics, setAnalytics] = useState<any | null>(null)
  const [loading, setLoading] = useState(true)
  const [analyticsLoading, setAnalyticsLoading] = useState(false)
  const [error, setError] = useState<string | null>(null)
  const [granularity, setGranularity] = useState<'hour' | 'day' | 'week'>('day')
  const [days, setDays] = useState(7)

  useEffect(() => {
    loadEndpoints()
  }, [])

  useEffect(() => {
    if (selectedEndpoint) {
      loadAnalytics()
    }
  }, [selectedEndpoint, granularity, days])

  const loadEndpoints = async () => {
    try {
      setLoading(true)
      setError(null)
      const response = await endpointsApi.list()
      setEndpoints(response.endpoints || [])
      if (response.endpoints && response.endpoints.length > 0 && !selectedEndpoint) {
        setSelectedEndpoint(response.endpoints[0].id)
      }
    } catch (err) {
      handleError(err)
    } finally {
      setLoading(false)
    }
  }

  const loadAnalytics = async () => {
    if (!selectedEndpoint) return
    
    try {
      setAnalyticsLoading(true)
      setError(null)
      
      const endDate = new Date()
      const startDate = new Date()
      startDate.setDate(startDate.getDate() - days)
      
      const data = await analyticsApi.getEndpointAnalytics(selectedEndpoint, {
        startDate: startDate.toISOString(),
        endDate: endDate.toISOString(),
        granularity,
        includeDetails: true,
      })
      
      setAnalytics(data)
    } catch (err) {
      handleError(err)
    } finally {
      setAnalyticsLoading(false)
    }
  }

  const handleError = (err: any) => {
    if (err instanceof ApiError) {
      const errorMsg = err.status === 401 
        ? 'Invalid API key. Please check your API key and try again.'
        : err.status === 403
        ? 'Access denied. Your API key may not have the required permissions.'
        : `${err.message} (Status: ${err.status}, Code: ${err.code})`
      setError(errorMsg)
    } else if (err instanceof Error) {
      if (err.message.includes('Failed to fetch') || err.message.includes('NetworkError')) {
        setError('Cannot connect to server. Make sure the server is running on http://localhost:3001')
      } else {
        setError(`Failed to load data: ${err.message}`)
      }
    } else {
      setError('Failed to load data: Unknown error')
    }
  }

  const formatNumber = (num: number) => {
    return new Intl.NumberFormat().format(num)
  }

  const formatCurrency = (amount: string) => {
    const num = parseFloat(amount) / 1e6 // Convert atomic units to USDC
    return `$${num.toFixed(6)}`
  }

  const formatLatency = (ms: number) => {
    if (ms < 1000) return `${Math.round(ms)}ms`
    return `${(ms / 1000).toFixed(2)}s`
  }

  if (loading) {
    return (
      <div className="text-center py-12">
        <Loader2 className="w-8 h-8 animate-spin mx-auto text-lime-400" />
        <p className="mt-4 text-gray-400">Loading endpoints...</p>
      </div>
    )
  }

  return (
    <div>
      <div className="flex items-center justify-between mb-6">
        <h1 className="text-3xl font-bold">Analytics</h1>
        <div className="flex items-center gap-4">
          <select
            value={days}
            onChange={(e) => setDays(parseInt(e.target.value))}
            className="px-4 py-2 bg-gray-800 border border-gray-700 rounded-lg text-white"
          >
            <option value={1}>Last 24 hours</option>
            <option value={7}>Last 7 days</option>
            <option value={30}>Last 30 days</option>
            <option value={90}>Last 90 days</option>
          </select>
          <select
            value={granularity}
            onChange={(e) => setGranularity(e.target.value as any)}
            className="px-4 py-2 bg-gray-800 border border-gray-700 rounded-lg text-white"
          >
            <option value="hour">Hourly</option>
            <option value="day">Daily</option>
            <option value="week">Weekly</option>
          </select>
        </div>
      </div>

      {error && (
        <div className="mb-6 p-4 bg-red-900/20 border border-red-800 rounded-lg flex items-center gap-3">
          <AlertCircle className="w-5 h-5 text-red-400" />
          <span className="text-red-400">{error}</span>
        </div>
      )}

      {/* Endpoint Selector */}
      {endpoints.length > 0 && (
        <div className="mb-6">
          <label className="block text-sm font-medium text-gray-300 mb-2">
            Select Endpoint
          </label>
          <select
            value={selectedEndpoint || ''}
            onChange={(e) => setSelectedEndpoint(e.target.value)}
            className="w-full md:w-96 px-4 py-2 bg-gray-800 border border-gray-700 rounded-lg text-white"
          >
            {endpoints.map((ep) => (
              <option key={ep.id} value={ep.id}>
                {ep.name} ({ep.id})
              </option>
            ))}
          </select>
        </div>
      )}

      {endpoints.length === 0 ? (
        <div className="text-center py-12 bg-gray-900 rounded-lg border border-gray-800">
          <BarChart3 className="w-12 h-12 text-gray-600 mx-auto mb-4" />
          <p className="text-gray-400 mb-4">No endpoints available</p>
          <p className="text-sm text-gray-500">Create an endpoint to see analytics</p>
        </div>
      ) : analyticsLoading ? (
        <div className="text-center py-12">
          <Loader2 className="w-8 h-8 animate-spin mx-auto text-lime-400" />
          <p className="mt-4 text-gray-400">Loading analytics...</p>
        </div>
      ) : analytics ? (
        <div className="space-y-6">
          {/* Summary Cards */}
          <div className="grid grid-cols-1 md:grid-cols-4 gap-4">
            <div className="bg-gray-900 rounded-lg border border-gray-800 p-6">
              <div className="flex items-center gap-2 mb-2">
                <TrendingUp className="w-5 h-5 text-lime-400" />
                <span className="text-sm text-gray-400">Total Requests</span>
              </div>
              <p className="text-2xl font-bold">{formatNumber(analytics.summary?.totalRequests || 0)}</p>
            </div>
            <div className="bg-gray-900 rounded-lg border border-gray-800 p-6">
              <div className="flex items-center gap-2 mb-2">
                <CheckCircle className="w-5 h-5 text-green-400" />
                <span className="text-sm text-gray-400">Total Payments</span>
              </div>
              <p className="text-2xl font-bold">{formatNumber(analytics.summary?.totalPayments || 0)}</p>
            </div>
            <div className="bg-gray-900 rounded-lg border border-gray-800 p-6">
              <div className="flex items-center gap-2 mb-2">
                <DollarSign className="w-5 h-5 text-cyan-400" />
                <span className="text-sm text-gray-400">Total Revenue</span>
              </div>
              <p className="text-2xl font-bold">{formatCurrency(analytics.summary?.totalRevenue || '0')}</p>
            </div>
            <div className="bg-gray-900 rounded-lg border border-gray-800 p-6">
              <div className="flex items-center gap-2 mb-2">
                <Clock className="w-5 h-5 text-blue-400" />
                <span className="text-sm text-gray-400">Avg Latency</span>
              </div>
              <p className="text-2xl font-bold">{formatLatency(analytics.summary?.averageResponseTime || 0)}</p>
            </div>
          </div>

          {/* Success/Error Rates */}
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <div className="bg-gray-900 rounded-lg border border-gray-800 p-6">
              <h3 className="text-lg font-semibold mb-4">Success Rate</h3>
              <div className="flex items-center gap-4">
                <div className="flex-1">
                  <div className="h-4 bg-gray-800 rounded-full overflow-hidden">
                    <div
                      className="h-full bg-green-500 transition-all"
                      style={{ width: `${(analytics.summary?.successRate || 0) * 100}%` }}
                    />
                  </div>
                </div>
                <span className="text-2xl font-bold text-green-400">
                  {((analytics.summary?.successRate || 0) * 100).toFixed(1)}%
                </span>
              </div>
            </div>
            <div className="bg-gray-900 rounded-lg border border-gray-800 p-6">
              <h3 className="text-lg font-semibold mb-4">Error Rate</h3>
              <div className="flex items-center gap-4">
                <div className="flex-1">
                  <div className="h-4 bg-gray-800 rounded-full overflow-hidden">
                    <div
                      className="h-full bg-red-500 transition-all"
                      style={{ width: `${(analytics.summary?.errorRate || 0) * 100}%` }}
                    />
                  </div>
                </div>
                <span className="text-2xl font-bold text-red-400">
                  {((analytics.summary?.errorRate || 0) * 100).toFixed(1)}%
                </span>
              </div>
            </div>
          </div>

          {/* Performance Metrics */}
          {analytics.performance && (
            <div className="bg-gray-900 rounded-lg border border-gray-800 p-6">
              <h3 className="text-lg font-semibold mb-4">Performance Metrics</h3>
              <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
                <div>
                  <span className="text-sm text-gray-400">P50 Latency</span>
                  <p className="text-xl font-bold">{formatLatency(analytics.performance.latency?.p50 || 0)}</p>
                </div>
                <div>
                  <span className="text-sm text-gray-400">P95 Latency</span>
                  <p className="text-xl font-bold">{formatLatency(analytics.performance.latency?.p95 || 0)}</p>
                </div>
                <div>
                  <span className="text-sm text-gray-400">P99 Latency</span>
                  <p className="text-xl font-bold">{formatLatency(analytics.performance.latency?.p99 || 0)}</p>
                </div>
                <div>
                  <span className="text-sm text-gray-400">Average</span>
                  <p className="text-xl font-bold">{formatLatency(analytics.performance.latency?.average || 0)}</p>
                </div>
              </div>
            </div>
          )}

          {/* Breakdowns */}
          {analytics.breakdowns && (
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              <div className="bg-gray-900 rounded-lg border border-gray-800 p-6">
                <h3 className="text-lg font-semibold mb-4">By Status</h3>
                <div className="space-y-2">
                  {Object.entries(analytics.breakdowns.byStatus || {}).map(([status, count]: [string, any]) => (
                    <div key={status} className="flex items-center justify-between">
                      <span className="text-gray-400 capitalize">{status}</span>
                      <span className="font-semibold">{formatNumber(count)}</span>
                    </div>
                  ))}
                </div>
              </div>
              <div className="bg-gray-900 rounded-lg border border-gray-800 p-6">
                <h3 className="text-lg font-semibold mb-4">By Facilitator</h3>
                <div className="space-y-2">
                  {Object.entries(analytics.breakdowns.byFacilitator || {}).map(([facilitator, count]: [string, any]) => (
                    <div key={facilitator} className="flex items-center justify-between">
                      <span className="text-gray-400">{facilitator}</span>
                      <span className="font-semibold">{formatNumber(count)}</span>
                    </div>
                  ))}
                </div>
              </div>
            </div>
          )}

          {/* Time Series Table */}
          {analytics.timeSeries && Object.keys(analytics.timeSeries).length > 0 && (
            <div className="bg-gray-900 rounded-lg border border-gray-800 p-6">
              <h3 className="text-lg font-semibold mb-4">Time Series Data</h3>
              <div className="overflow-x-auto">
                <table className="w-full text-sm">
                  <thead>
                    <tr className="border-b border-gray-800">
                      <th className="text-left py-2 text-gray-400">Time</th>
                      <th className="text-right py-2 text-gray-400">Requests</th>
                      <th className="text-right py-2 text-gray-400">Payments</th>
                      <th className="text-right py-2 text-gray-400">Revenue</th>
                      <th className="text-right py-2 text-gray-400">Errors</th>
                      <th className="text-right py-2 text-gray-400">Avg Latency</th>
                    </tr>
                  </thead>
                  <tbody>
                    {Object.entries(analytics.timeSeries)
                      .sort(([a], [b]) => a.localeCompare(b))
                      .map(([time, data]: [string, any]) => (
                        <tr key={time} className="border-b border-gray-800">
                          <td className="py-2 text-gray-300">{new Date(time).toLocaleString()}</td>
                          <td className="py-2 text-right">{formatNumber(data.requests || 0)}</td>
                          <td className="py-2 text-right">{formatNumber(data.payments || 0)}</td>
                          <td className="py-2 text-right">{formatCurrency(data.revenue || '0')}</td>
                          <td className="py-2 text-right text-red-400">{formatNumber(data.errors || 0)}</td>
                          <td className="py-2 text-right">{formatLatency(data.averageLatency || 0)}</td>
                        </tr>
                      ))}
                  </tbody>
                </table>
              </div>
            </div>
          )}
        </div>
      ) : (
        <div className="text-center py-12 bg-gray-900 rounded-lg border border-gray-800">
          <BarChart3 className="w-12 h-12 text-gray-600 mx-auto mb-4" />
          <p className="text-gray-400">Select an endpoint to view analytics</p>
        </div>
      )}
    </div>
  )
}

